.container {
    display: block;
    margin: auto;
    border-radius: 20px;
    min-width: 300px;
    max-width: 400px;
    padding: 20px;
    text-align: center;
    background: -webkit-gradient(linear, left top, left bottom, from(hsla(9, 90%, 68%, 0.8)), to(hsla(9, 90%, 60%, 0.8))) ;
    background: -webkit-linear-gradient(hsla(9, 90%, 68%, 0.8), hsla(9, 90%, 60%, 0.8)) ;
    background: -o-linear-gradient(hsla(9, 90%, 68%, 0.8), hsla(9, 90%, 60%, 0.8)) ;
    background: linear-gradient(hsla(9, 90%, 68%, 0.8), hsla(9, 90%, 60%, 0.8)) ;
    -webkit-box-shadow: 0 0 10px rgba(0, 0, 0, 0.4),
    inset 2px 2px 4px rgba(255, 255, 255, .6),
                inset -2px -2px 4px rgba(0, 0, 0, 0.6);
            box-shadow: 0 0 10px rgba(0, 0, 0, 0.4),
    inset 2px 2px 4px rgba(255, 255, 255, .6),
                inset -2px -2px 4px rgba(0, 0, 0, 0.6);
}


